import MetaTags from "common/MetaTags";
import { Page } from "common/components";
import { useTranslation } from "react-i18next";
import { getDescription } from "../descriptions";
import FAQAr from "./faq-ar";
import FAQEn from "./faq-en";

const FAQ = ({ contentId, titleId }) => {
  const [t, i18n] = useTranslation();

  const getTitle = () => t(titleId);

  const metaTags = [
    {
      name: "description",
      content: getDescription(contentId, i18n.language),
    },
  ];

  return (
    <Page title={getTitle()}>
      <MetaTags title={getTitle()} tags={metaTags} />
      <div className="content">
        <div className="container">
          <div className="row">
            <div className="col-12">
              {i18n.language === "ar" ? (
                <FAQAr title={getTitle()} />
              ) : (
                <FAQEn title={getTitle()} />
              )}
            </div>
          </div>
        </div>
      </div>
    </Page>
  );
};

export default FAQ;
